import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, EmpresaMHO } from '../../../../models/dtos/mho/empresaMHO';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'empresa.codigo',
    order: OrderType.DESC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const EmpresaMHOSearchView: FC<SearchViewProps<EmpresaMHO>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'empresa.codigo',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'empresa.razaoSocial',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Razão Social',
    },
    {
      dataField: 'diaVencimento',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Dia do Vencimento',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Configuração de Empresas">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, '', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default EmpresaMHOSearchView;
